@import url('https://fonts.googleapis.com/css?family=Bangers');

/* pageBackground */
html {
    /* background: radial-gradient(#ba3e7d, #9d38d2) */
    background: -webkit-radial-gradient(top, ellipse, #ba3e7d, transparent),
            -webkit-radial-gradient(bottom, ellipse, #9d38d2, transparent);
    background: -o-radial-gradient(top, ellipse, #ba3e7d, transparent),
            -o-radial-gradient(bottom, ellipse, #9d38d2, transparent);
    background: radial-gradient(ellipse at top, #ba3e7d, transparent),
            radial-gradient(ellipse at bottom, #9d38d2, transparent);
}


html, body, #root {
    height: 100%;
}

body {
    overflow: hidden;
    margin: 0;
    padding: 0;
    font-family: 'Bangers', cursive;
    letter-spacing: 1px;
    color: black;
  /* font-family: sans-serif; */
}


.menuSelectors {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 100%;
    padding: 10px;
}
.menuSelectors > *, .link {
    color: black;
    text-decoration: none;
    font-size: 1.35rem;
}

.selectionArea {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: center;
        justify-content: center;
    padding: 10px;
    margin: 10px;
}
.selectionArea > .title {
    margin: 5px;
}

.difficultiesWrapper {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: center;
        justify-content: center;
    /* padding: 10px; */
    margin: 10px;
    background: -webkit-gradient(linear, left top, right top, from(transparent), color-stop(rgba(173, 216, 230, 0.5)), to(transparent));
    background: -webkit-linear-gradient(left, transparent, rgba(173, 216, 230, 0.5), transparent);
    background: -o-linear-gradient(left, transparent, rgba(173, 216, 230, 0.5), transparent);
    background: linear-gradient(to right, transparent, rgba(173, 216, 230, 0.5), transparent);
}
.playerSelectorsWrapper {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: center;
        justify-content: center;
    padding: 0 10px;
    margin: 10px;
    background: -webkit-gradient(linear, left top, right top, from(transparent), color-stop(rgba(173, 216, 230, 0.5)), to(transparent));
    background: -webkit-linear-gradient(left, transparent, rgba(173, 216, 230, 0.5), transparent);
    background: -o-linear-gradient(left, transparent, rgba(173, 216, 230, 0.5), transparent);
    background: linear-gradient(to right, transparent, rgba(173, 216, 230, 0.5), transparent);
}
.playerSelector {
    margin: 8px 15px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    text-align: center;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
}
.playerSelector:hover, .playerSelector:active {
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
    cursor: pointer;
    /* transform: scale3d(1, 1.2, 1.5); */
}
.playerSelectorLogo {
    width: 55px;
    height: auto;
}
.playerSelectorTitle {
    margin: 10px 0;
}
.selected, .selected > * {
    color: #09e905 !important ;
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
}

.settingsBtn {
    position: absolute;
    top: 25px;
    right: 20px;
    color: black;
    text-decoration: none;
}

.playBtn {
    background: #09e905;
    padding: 12px 25px;
    color: black;
    margin: 1rem;
}
.playBtn:hover {
    background: #05b202;
}


/* Game classes */
.scoreDisplay {
    margin: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
    padding: 0 10px;
}
.exitBtn {
    padding: 5px 10px;
    background: #09e905;
    border: none;
    max-height: 35px;
    margin-top: 10px;
    font-weight: 500;
    font-size: 1rem;
    font-family: 'Bangers', cursive;
    letter-spacing: 1px
}

#p5_loading {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    font-size: 1.5rem;
}

/* settings */
.difficultyBtn {
    margin: 10px;
}
.difficultyBtn:hover {
    cursor: pointer;
}


